:root {
  --color--background: #fff;
  --color--text: #000;
  --color--accent: #6a00ff;
}

html, body {
  margin: 0;
}

html {
  color: #000;
  font-family: sans-serif;
  line-height: 1.3;
}

header, main, footer {
  padding: 2rem;
}

@media (width <= 800px) {
  :is(header, main, footer) {
    padding: 1rem;
  }
}

h1, h2, h3, h4 {
  margin: 0 0 2em;
  line-height: 1.1;
}

h1 {
  letter-spacing: -.025em;
  font-family: serif;
  font-size: 4rem;
  font-weight: 400;
}

@media (width <= 800px) {
  h1 {
    font-size: 2.5rem;
  }
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  max-width: 54rem;
  margin: 1rem 0;
}

a {
  color: inherit;
  text-underline-offset: .15em;
  text-underline-thickness: 1px;
  text-decoration: underline #00000040;
}

@media (hover: hover) and (pointer: fine) {
  a:hover {
    text-decoration-color: #000;
  }
}

button, input[type="button"], input[type="submit"], .button {
  font: inherit;
  color: var(--color--background);
  text-transform: uppercase;
  letter-spacing: .06em;
  background-color: var(--color--text);
  box-shadow: none;
  cursor: pointer;
  border: none;
  border-radius: 2em;
  outline: none;
  padding: .5em .8em .4em;
  font-size: .8rem;
  line-height: 1;
  text-decoration: none;
}

@media (hover: hover) and (pointer: fine) {
  :is(button, input[type="button"], input[type="submit"], .button):hover {
    color: var(--color--background);
    background-color: var(--color--accent);
  }
}

code {
  background-color: #eee;
  border-radius: .5rem;
  max-width: 54rem;
  margin: 1rem 0;
  padding: .5rem;
  display: block;
}

code pre {
  margin: 0;
}

.Items {
  grid-gap: 2vw;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: min-content;
  align-content: flex-start;
  align-items: flex-start;
  gap: 2vw;
  width: 100%;
  display: grid;
}

@media (width <= 800px) {
  .Items {
    grid-template-columns: 1fr;
    grid-auto-rows: min-content;
    gap: 50vw;
  }
}

.Item {
  aspect-ratio: 1;
  background-image: linear-gradient(#fff 0%, #666 100%);
  border-radius: .5rem;
  margin: 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 .2rem 1rem #0006;
}

@media (width <= 800px) {
  .Item {
    grid-column: initial !important;
    grid-row: initial !important;
    margin: 0 !important;
  }
}

